*{box-sizing:border-box}html{color:#354a5f;font-family:sans-serif;font-size:1rem;line-height:1.4;font-size:14px}body{margin:0;background:#eee;overflow-y:scroll}.app{width:100%;max-width:50rem;margin:0 auto;padding:0.5rem;box-sizing:border-box}.app .content{display:block}.app .search{display:none}.app.-searching .content{display:none}.app.-searching .search{display:block}.header{margin-bottom:0.5rem;display:flex;flex-direction:row;border-radius:3px;overflow:hidden;box-sizing:border-box}.header a{padding:0.5rem 1rem;background:#7ea8e2;text-decoration:none !important}.header a+a{border-left:1px solid #a7c2e8}.header input{flex-grow:1;border:none;padding-left:1rem;padding-right:1rem;border-color:#fff;border-width:1px 1px 1px 0;border-top-right-radius:3px;border-bottom-right-radius:3px;border-style:solid;min-width:0;color:#354a5f;font-family:sans-serif;font-size:1rem;line-height:1.4}.header input:focus{outline:none;border-color:#7ea8e2}.content,.search{width:100%;box-sizing:border-box;padding:0 1rem;border:1px solid #ccc;background:#fff;border-radius:3px;overflow-x:auto}.content a,.content a:visited,.search a,.search a:visited{color:#467fcf;text-decoration:none}.content a:hover,.search a:hover{text-decoration:underline}.content a.external,.content a.external:visited,.search a.external,.search a.external:visited{color:#6e54ba}.content a.missing,.search a.missing{color:#d32f2f}.content a.current,.search a.current{font-weight:bold}.content li p,.search li p{margin:0}.content blockquote,.search blockquote{border-left:0.25rem solid rgba(0,0,0,0.07);padding-left:0.5rem;margin:0}.reference{border-bottom:1px solid rgba(0,0,0,0.07);padding:1rem 0}.reference h3{margin-top:0}.bookmark{padding:1rem 0;border-bottom:1px solid rgba(0,0,0,0.07)}.bookmark__title{margin:0}.bookmark__url{font-size:0.875rem;color:#778592}.bookmark__tags{padding:0.5rem 0}.bookmark__tags a{margin-right:0.5rem}.bookmark__description p{margin:0 0 0.5rem 0}.tweet{padding:1rem 0;border-bottom:1px solid rgba(0,0,0,0.07)}.tweet__url{font-size:0.875rem;color:#778592}.tweet__tags{padding:0.5rem 0}.tweet__tags a{margin-right:0.5rem}.tweet__tweet{border-left:0.25rem solid rgba(0,0,0,0.07);padding-left:0.5rem}.tweet__notes p{margin:0 0 0.5rem 0}
